export default "import React, { useCallback, useEffect, useState } from 'react';\n\nexport const useScrollBar = (emblaApi) => {\n    const [value, setValue] = useState(0);\n    \n    const scrollToProgress = useCallback((progress) => {\n        if (!emblaApi)\n            return;\n        const { animation, limit, target, scrollProgress, scrollBody, scrollTo } = emblaApi.internalEngine();\n        \n        animation.stop();\n        \n        const currentProgress = scrollProgress.get(target);\n        const allowedProgress = Math.min(Math.max(progress, 0), 1);\n        const progressToTarget = allowedProgress - currentProgress;\n        const distance = progressToTarget * limit.length * -1;\n        \n        scrollBody.useDuration(0);\n        scrollTo.distance(distance, false);\n    }, [emblaApi]);\n    \n    const onScrollBarChange = useCallback((event) => {\n        if (!emblaApi)\n            return;\n        \n        const newProgress = parseFloat(event.target.value);\n        setValue(newProgress);\n        scrollToProgress(newProgress);\n    }, [emblaApi, scrollToProgress]);\n    \n    useEffect(() => {\n        if (!emblaApi)\n            return;\n        \n        setValue(emblaApi.scrollProgress());\n        \n        emblaApi.on('scroll', (emblaApi) => {\n            setValue(emblaApi.scrollProgress());\n        });\n    }, [emblaApi]);\n    \n    return {\n        value,\n        onScrollBarChange\n    };\n};\n\nexport const ScrollBar = (props) => {\n    const { value, onChange } = props;\n    \n    return (<input className=\"embla__scrollbar\" onChange={onChange} type=\"range\" id=\"slider\" min=\"0\" max=\"1\" step=\"0.001\" value={value}/>);\n};\n";